import { dataJSON } from './render.js';

const params = new URLSearchParams(window.location.search);
const productId = parseInt(params.get('id'), 10);

const product = dataJSON.find(item => item.id === productId);

if (product) {
  document.title = product.name; // Змінюємо заголовок на назву продукту

  const contentDiv = document.querySelector('.content');

  contentDiv.innerHTML = `
    <h1>${product.name}</h1>
    <img src="${product.image}" alt="${product.name}" />
    <p>${product.subtitle}</p>
    <p class="price">${product.price}</p>
    ${product.strikethrough ? `<p class="strikethrough">${product.strikethrough}</p>` : ''}
    ${product.discount ? `<p class="discount">${product.discount}</p>` : ''}
    ${product.new ? `<p class="new">${product.new}</p>` : ''}
  `;
} else {
  contentDiv.innerHTML = `<p>Product not found.</p>`;
}
